<template>
<div class="container">
<figure class="signup-logo text-center mt-5">
    <img src="../assets/logo.png" alt="">
</figure>
    <h2 class="text-center">Sign Up</h2>
    <div class="mt-4 signup-form-wrapper">
        <div class="form-group mb-3">
            <label for="">Name</label>
            <input type="text" name="name" class="form-control" v-model="name">
        </div>
        <div class="form-group mb-3">
            <label for="">Email</label>
            <input type="email" name="email" class="form-control" v-model="email">
        </div>
        <div class="form-group mb-3">
            <label for="">Password</label>
            <input type="password" name="password" class="form-control" v-model="password">
        </div>
        <div class="text-end mt-4">
            <button class="btn btn-outline-success w-100" v-on:click="signUp()">Sign Up</button>
        </div>
        <div class="mt-3 pt-3 border-top text-center">
            already sign up? <router-link to="Login" class="text-primary">Login</router-link>
        </div>
    </div>
    
</div>
</template>

<script>
import axios from "axios";
export default {
    name:"SignUp",
    data(){
        return{
            name:"",
            email:"",
            password:""
        }
    },
    methods:{
        async signUp(){
            let result = await axios.post('http://localhost:3000/users/',{
                name:this.name,
                email:this.email,
                password:this.password
            });
            console.warn(">>>>>", result);
            alert("Sign Up Successfully");
            if(result.status==201){
                localStorage.setItem("user-info", JSON.stringify(result.data))
                this.$router.push({name:"Home"})
            }
        }
    },
    mounted(){
        let user=localStorage.getItem('user-info');
        if(user){
            this.$router.push({name:"Home"})
        }
    }
}
</script>
