<template>
<div class="container">
    <figure class="signin-logo text-center mt-5">
        <img src="../assets/logo.png" alt="">
    </figure>
    <h2 class="text-center">Login</h2>
    <div class="mt-4 login-form-wrapper">
        <div class="form-group mb-3">
            <label for="">Email</label>
            <input type="email" name="email" class="form-control" v-model="email">
        </div>
        <div class="form-group mb-3">
            <label for="">Password</label>
            <input type="password" name="password" class="form-control" v-model="password">
        </div>
        <div class="text-end mt-4">
            <button class="btn btn-outline-success w-100" v-on:click="userLogin()">Login</button>
        </div>
        <div class="mt-3 pt-3 border-top text-center">
            not sign up? <router-link to="sign-up" class="text-primary">Sign Up Now</router-link>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
export default {
    name: "Login",
    data(){
        return{
            email:"",
            password:""
        }
    },
    methods:{
        async userLogin(){
            //alert(1)
            let result = await axios.get(`http://localhost:3000/users/?email=${this.email}&password=${this.password}`)

            if(result.status==200 && result.data.length>0)
            {
                localStorage.setItem("user-info",JSON.stringify(result.data[0]))
                this.$router.push({name:"Home"})
            }
            else{
                alert("Wrong Credentials")
            }
            //console.warn(result);
        }
    },
    mounted(){
        let user=localStorage.getItem('user-info');
        if(user){
            this.$router.push({name:"Home"})
        }
    }
}
</script>
