<template>
<Header/>
<div class="container">
    <div class="text-center mt-3">
         <h3>Update Restaurant</h3>
    </div>
    <div class="row mt-4">
        <div class="offset-lg-3 col-lg-6">
            <form>
        <div class="form-group mb-3">
            <label for="">Restaurant Name</label>
            <input type="text" placeholder="type..." class="form-control" v-model="restaurant.name">
        </div>
        <div class="form-group mb-3">
            <label for="">Restaurant Contact</label>
            <input type="text" placeholder="type..." class="form-control" v-model="restaurant.contact">
        </div>
        <div class="form-group mb-3">
            <label for="">Restaurant Address</label>
            <input type="text" placeholder="type..." class="form-control" v-model="restaurant.address">
        </div>
        <div class="text-end mt-4">
            <button type="button" class="btn btn-success" v-on:click="updateRestaurant">Update Restaurant</button>
        </div>
    </form>
        </div>
    </div>
</div>
    
</template>

<script>
import axios from 'axios'
import Header from "./Header.vue"
export default {
    name:"Update",
    data(){
        return{
            restaurant:{
            name:" ",
            contact:" ",
            address:" "
            }
        }
    },
    methods:{
        async updateRestaurant(){
            console.warn(this.restaurant)
            let result = await axios.put('http://localhost:3000/restaurant/'+this.$route.params.id,{
                name:this.restaurant.name,
                contact:this.restaurant.contact,
                address:this.restaurant.address
            });
            // console.warn("Udated Restaurant>>>>>", result);
            if(result.status==200){
                //localStorage.setItem("user-info", JSON.stringify(result.data))
                this.$router.push({name:"Home"})
            }
        }
    },
    components:{
        Header
    },
    async mounted(){
        let user=localStorage.getItem('user-info');
        if(!user){
            this.$router.push({name:"Login"})
        }
    const result = await axios.get('http://localhost:3000/restaurant/'+this.$route.params.id);
    //console.warn("%%%%", result.data)
    this.restaurant=result.data
    }
}
</script>