<template>
<Header />
<div class="container">
    <div class="text-center my-3">
        <h3>Welcome <span class="fw-bold text-warning">{{name}}</span></h3>
    </div>
    <h5>Restaurant List</h5>
    <div class="table-responsive mt-2">
        <table class="table table-bordered table-stript">
            <thead>
                <tr>
                    <td class="text-center">Id</td>
                    <td>Name</td>
                    <td>Contact</td>
                    <td>Address</td>
                    <td class="text-center">Actions</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in restaurant" :key="item.id">
                    <td class="text-center">{{item.id}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.contact}}</td>
                    <td>{{item.address}}</td>
                    <td class="text-center">
                        <div class="d-flex justify-content-center">
                            <router-link class="nav-link px-2 py-1 align-self-center" :to="'/update/'+item.id"><i class="text-secondary fas fa-pencil-alt"></i></router-link>
                            <button type="button" class="btn px-2 py-1 nav-link align-self-center cursor-pointer" v-on:click="deleteRestaurant(item.id)"><i class="text-danger fas fa-trash-alt"></i></button>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</div>
</template>

<script>
import Header from "./Header.vue";
import axios from "axios";
export default {
    name: "Home",
    data() {
        return {
            name: " ",
            restaurant: [],
        }
    },
    components: {
        Header
    },
    methods: {
        
        
        async deleteRestaurant(id) {
            let result=await axios.delete("http://localhost:3000/restaurant/"+id);
            console.warn(result)
            if (result.status == 200) {
              this.loadData()  
            }
        },
        async loadData() {
            let user = localStorage.getItem('user-info')
            this.name = JSON.parse(user).name
            if (!user) {
                this.$router.push({
                    name: "Login"
                })
            }
            let result = await axios.get("http://localhost:3000/restaurant")
            console.warn(">>>>List>>>>", result)
            this.restaurant = result.data;
        }
    },
    async mounted() {
        this.loadData()  
    }
}
</script>
