<template>
<Header/>
<div class="container">
    <div class="text-center mt-3">
        <h3>Add Restaurant</h3>
    </div>
    <div class="row mt-4">
        <div class="offset-lg-3 col-lg-6">
            <form>
        <div class="form-group mb-3">
            <label for="">Restaurant Name</label>
            <input type="text" placeholder="type..." name="name" class="form-control" v-model="name">
        </div>
        <div class="form-group mb-3">
            <label for="">Restaurant Contact</label>
            <input type="text" placeholder="type..." name="contact" class="form-control" v-model="contact">
        </div>
        <div class="form-group mb-3">
            <label for="">Restaurant Address</label>
            <input type="text" placeholder="type..." name="address"  class="form-control" v-model="address">
        </div>
        <div class="text-end mt-4">
            <button type="button" class="btn btn-success" v-on:click="addRestaurant">Add Restaurant</button>
        </div>
    </form>
        </div>
    </div>
</div>
    
</template>

<script>
import axios from "axios";
import Header from "./Header.vue"
export default {
    name:"Add",
    data(){
        return{
            name:"",
            contact:"",
            address:""
        }
    },
    components:{
        Header
    },
    methods:{
        async addRestaurant(){
            let result = await axios.post('http://localhost:3000/restaurant',{
                name:this.name,
                contact:this.contact,
                address:this.address
            });
            console.warn("Added Restaurant>>>>>", result);
            if(result.status==201){
                //localStorage.setItem("user-info", JSON.stringify(result.data))
                this.$router.push({name:"Home"})
            }
        }
    },
    mounted(){
        let user=localStorage.getItem('user-info');
        if(!user){
            this.$router.push({name:"Login"})
        }
    }
}
</script>